@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
.navbar-wrapper {
  width: 100%;
  height: 80px;
  font-family: "Didact Gothic", sans-serif;
  border-bottom: 1px solid #a9b1d3; }
  .navbar-wrapper .navbar-logo {
    margin: 20px 0 0 0;
    float: left; }
  .navbar-wrapper .navbar-tabs {
    float: right; }
    .navbar-wrapper .navbar-tabs .navbar-tabitem {
      display: inline-block;
      margin: 30px 0 auto 30px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 300;
      color: #273c92; }

.content-wrapper {
  width: 100%; }
  .content-wrapper .title {
    margin: 10px 0;
    display: inline-block;
    font-family: "Didact Gothic", sans-serif;
    font-size: 24px;
    color: #273c92; }
  .content-wrapper .map {
    width: 100%;
    height: 600px;
    position: relative; }
  .content-wrapper .control {
    z-index: 3;
    position: absolute;
    left: 10px;
    top: 60px; }
  .content-wrapper .selector {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .content-wrapper .selector .handler {
      position: absolute;
      height: 12px;
      width: 12px;
      background-color: white;
      border: solid 1px #273c92;
      cursor: move; }
    .content-wrapper .selector .shader {
      background-color: rgba(0, 0, 0, 0.15); }

body {
  margin: 0; }

.content {
  width: 83.3333%;
  margin: 0 auto; }

